.form {
  width: 240px;
  padding: 20px 10px;
  border: 1px solid #404040;

  display: flex;
  flex-direction: column;
}

.label {
  margin-bottom: 16px;
  color: #404040;
}

.input {
  margin-left: 20px;
  outline: none;
  border: 1px solid #404040;
}

.input:hover,
.input:focus {
  border: 1px solid limegreen;
}

.input_number {
  margin-left: 4px;
  outline: none;
  border: 1px solid #404040;
}
.input_number:hover,
.input_number:focus {
  border: 1px solid limegreen;
}
.button {
  padding: 2px 6px;
  color: #404040;
  border: 1px solid #404040;
  cursor: pointer;
  outline: none;
  font-size: 14px;
}
.button:hover,
.button:focus {
  border: 1px solid limegreen;
}
